import React, { useEffect } from 'react';
import './style.scss';

export default ({loaded, active}) => {
    useEffect(() => {
        if(loaded) {
            const bg = document.getElementsByClassName('background-column-1')[0];
            bg.style.left = '0';
            setTimeout(() => slideBlocksIn() , 500);

            changeMobile(active);
        }
    }, [loaded, active]);

    const slideBlocksIn = () => {
        const block1 = document.getElementsByClassName('background-block-1')[0];
        const block2 = document.getElementsByClassName('background-block-2')[0];

        block1.style.right = '0';
        block2.style.left = '0';
    }

    const changeMobile = (active) => {
        if(window.innerWidth < 768) {
            const block = document.getElementsByClassName('background-column-1')[0];

            if(active) {
                block.style.left = '-100%';
            } else {
                block.style.left = '0';
            }
        }
    }

    return (
        <div className="background">
            <div className='background-container'>
                <div className='background-column-1'></div>
                <div className='background-column-2'>
                    <img className='background-image' />
                </div>

                <div className="background-block-1"></div>
                <div className="background-block-2"></div>
            </div>
        </div>
    )
}