import React, {useEffect} from 'react';
import photo from '../../../assets/joao.jpg'; 
import '../content.scss';

export default ({setSection,loaded}) => {

    useEffect(() => {
        setTimeout(() => {
            fadeContentIn();
        }, 1000);
    }, [loaded])


    const fadeContentIn = () => {
        const contents = document.querySelectorAll('.right-content *');

        contents.forEach(item => {
            item.style.opacity = 1;
        })
    }

    return (
        <>
            <div className='col-md-7 left-content hidden-sm'>
                <h1 className='content-title'>
                    João <br />
                    Azevedo 
                </h1>

                <h2>
                    Psicólogo
                </h2>

                <h3>
                    CRP: 04/56886
                </h3>
                <button onClick={() => setSection(0)}>Voltar</button>
            </div>
            <div className='col-md-5 right-content'>

                <h4 className='show-sm'>
                    Sobre Mim
                </h4>


                <img className='bio-photo' src={photo} />

                <h4 className='hidden-sm'>
                    Sobre Mim
                </h4>

                <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Risus viverra adipiscing at in tellus integer feugiat scelerisque. Vestibulum morbi blandit cursus risus at ultrices mi tempus. In nulla posuere sollicitudin aliquam ultrices sagittis orci a scelerisque. Egestas egestas fringilla phasellus faucibus. Blandit turpis cursus in hac habitasse platea dictumst. Amet mattis vulputate enim nulla aliquet.
                </p>

                <button onClick={() => setSection(0)} className='dark show-sm'>Voltar</button>
            </div>
        </>
    )
}