import React from 'react';
import './index.scss'
import Home from '../content/home';
import Section1 from '../content/section1';


export default ({current, loaded, setSection, setAnimation, animationTriggered}) => {
    const renderContent = (current) => {
        switch (current) {
            case 0: 
                return <Home 
                animationTriggered={animationTriggered}
                setAnimation={setAnimation} setSection={setSection} loaded={loaded} />
                break;
            case 1: 
                return <Section1 setSection={setSection} loaded={loaded} />
                break;
            default:
                return <></>
        }
    }

    return (
        <div className='content-container container'>
            {
               renderContent(current)
            }
        </div>
    )
}