import React, { useEffect } from 'react';

import './index.scss';

export default ({loaded, setSection, setAnimation, animationTriggered}) => {
    useEffect(() => {
        if(loaded && !animationTriggered || window.innerWidth < 768) {
            hideText();
            setTimeout(() =>{fadeTextIn()}, 1000);
            setAnimation();
        }
    }, [loaded]);

    const hideText = () => {
        const content = document.querySelectorAll('.content-home *');

        for(let i = 0; i < content.length; i++) {
            content[i].style.opacity = 0;
        }
    }

    const fadeTextIn = () => {
        const content = document.querySelectorAll('.content-home *');
        

        for(let i = 0; i < 4; i++) {
            content[i].style.transition = 'ease-out all 1s';
            content[i].style.opacity = 1;
        }

        setTimeout(() => {
            for(let i = 4; i < content.length; i++) {
                content[i].style.transition = 'ease-out all 1s';
                content[i].style.opacity = 1;
            }
    
        }, 500);
    }

    return (
        <div className='content-home col-md-6'>
            <h1 className='content-title'>
                João <br />
                Azevedo 
            </h1>

            <h2>
                Psicólogo
            </h2>

            <h3>
                CRP: 04/56886
            </h3>

            {/* <p>O empenho em analisar a percepção das dificuldades promove a alavancagem dos relacionamentos verticais entre as hierarquias.</p> */}
        
            <div className='content-buttons'>
                <button onClick={() => setSection(1)} className='light'>Sobre mim</button>
                <button>Sobre mim</button> <br />
                <button>Sobre mim</button>
                <button>Sobre mim</button>
            </div>
        </div>
    )
}